import wx from 'weixin-js-sdk'
import { setTimeout } from 'timers';
export default {
    data() {
        return {
            isVoice:false, imgFile:[], isFile: true,
            recording:{
                START: 0, END: 0, recordTimer:'', localId:''
            }
        }
    },
    props:['voice'],
    components: {
        
    },
    beforeCreate(){
        
    },
    computed: {
        serverId(){
            return this.$store.state.serverId
        },
    },
    created(){
        // console.log(this.voice, this.voice != false ? true : this.voice)
        
        this.wx()
    },
    methods: {
        onRead(file) {
            // console.log(file)
            this.imgFile.push(file)
            this.$store.commit('imgFile_', this.imgFile)
        },
        voiceing(){
            this.isVoice = true
            setTimeout(()=>{
                this.isVoice = false
            },5000)
        },
        deletes(index){
            this.imgFile.splice(index,1)
            this.$store.commit('imgFile_', this.imgFile)
        },
        VoiceDelet(){
            this.$store.commit('serverID', '')
            this.recording.END = 0
            this.recording.START = 0
        },
        touchStart(){
            this.isVoice = true
            if(this.serverId == ''){
                this.recording.START = new Date().getTime()
                this.recording.recordTimer = setTimeout(()=>{
                    wx.startRecord({
                        success: res =>{
                            // console.log('')
                        },
                        cancel: function () {
                            alert('');
                        }
                    });
                },500);
            }else{
                wx.playVoice({
                    localId: this.recording.localId // IDstopRecord
                });
                let time = this.recording.END - this.recording.START
                setTimeout(()=>{
                    this.isVoice = false
                }, time)
            }
        },
        touchend(){
            if(this.serverId == ''){
                this.recording.END = new Date().getTime()
                if((this.recording.END - this.recording.START) < 500){
                    this.recording.END = 0
                    this.recording.START = 0
                    //300ms
                    clearTimeout(this.recording.recordTimer);
                }else{
                    this.isVoice = false
                    wx.stopRecord({
                        success: res=> {
                            // console.log(res)
                            this.recording.localId = res.localId;
                            this.uploadVoice();
                        },
                        fail: function (res) {
                            alert(JSON.stringify(res));
                        }
                    });
                }
            }
        },
        uploadVoice(){
            //
            //3
            wx.uploadVoice({
                localId: this.recording.localId, // IDstopRecord
                isShowProgressTips: 1, // 1
                success: res=> {
                    //idres.serverId
                    this.$store.commit('serverID', res.serverId)
                }
            });
        },
        wx(){
            this.$store.dispatch('shire')
            .then(res =>{
                wx.config({
                    beta: true,// wx.invokejsapi
                    debug: false, // ,apialertpclogpc
                    appId: res.corpid, // corpID
                    timestamp: res.timestamp, // 
                    nonceStr: res.nonce, // 
                    signature: res.signature,// 1
                    jsApiList: ['startRecord','stopRecord','uploadVoice'] // JSJS2
                });
                wx.ready(()=>{
                    // configreadyconfigconfigreadyready
                    
                    // console.log(1)
                    
                });
            })
            .catch(err => console.log(err))
        },
    },
    destroyed(){
        this.$store.commit('imgFile_', '')
        this.$store.commit('serverID', '')
    }
}



